body {
  margin: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
  background-color: white; }

img {
  display: block;
  max-width: 100%; }

a {
  display: block;
  text-decoration: none; }

html {
  -webkit-box-sizing: border-box;
          box-sizing: border-box; }

*,
*::before,
*::after {
  -webkit-box-sizing: inherit;
          box-sizing: inherit; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  -webkit-clip-path: inset(100%);
          clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.btn {
  display: block;
  padding: 14px 13px 8px 12px;
  font-size: 14px;
  font-weight: bold;
  line-height: 16px;
  text-align: center;
  text-transform: uppercase;
  text-decoration: none;
  letter-spacing: 0.05em;
  color: white;
  background-color: transparent;
  border: 1px solid white;
  border-radius: 5px;
  cursor: pointer; }

.theme-dark {
  background: #1b2d37; }
  .theme-dark h1,
  .theme-dark h2,
  .theme-dark h3,
  .theme-dark h4,
  .theme-dark h5,
  .theme-dark h6 {
    color: white; }
  .theme-dark p,
  .theme-dark a {
    color: white; }
  .theme-dark a:hover, .theme-dark a:focus {
    color: white; }
  .theme-dark a:active {
    color: white;
    opacity: 0.4; }

.theme-light {
  background: white; }
  .theme-light h1,
  .theme-light h2,
  .theme-light h3,
  .theme-light h4,
  .theme-light h5,
  .theme-light h6 {
    color: #666666; }
  .theme-light p,
  .theme-light a {
    color: #666666; }
  .theme-light a:hover, .theme-light a:focus {
    color: black; }
  .theme-light a:active {
    color: black;
    opacity: 0.4; }

